// Write your code here
import {Component} from 'react'

import './index.css'

import DestinationItem from '../DestinationItem/index.js'

class heading extends Component {
  state = {searchInput: ''}

  changingItems = event => this.setState({searchInput: event.target.value})

  render() {
    const {destinationsList} = this.props
    const {searchInput} = this.state
    const filterItems = destinationsList.filter(eachList =>
      eachList.name.toLowerCase().includes(searchInput.toLowerCase()),
    )

    return (
      <div className="background-card">
        <div className="card-container">
          <h1>Destination Search</h1>
          <div>
            <input
              type="search"
              value={searchInput}
              placeholder="Search"
              onChange={this.changingItems}
            />
            <img
              src="https://assets.ccbp.in/frontend/react-js/destinations-search-icon-img.png"
              alt="search icon"
              className="searchIcon"
            />
          </div>
          <ul className="items">
            {filterItems.map(eachList => (
              <DestinationItem nameList={eachList} key={eachList.id} />
            ))}
          </ul>
        </div>
      </div>
    )
  }
}

export default heading
